// 实训基地
import React, {Component} from 'react';
import {Col} from 'antd';
import  '../styles.less'

export default class TeachTeam extends Component {
  constructor(props) {
    super(props);
    this.state = {
      num:'232',
      workNum:'3688',
      Equipment:'12673.885',
      AverageInstrument:'12493.7',
      UtilizationRate:'89.72%',
      OutSchoolNum:'120'
    }
  }
  render(){
    return(
      <Col span={24} style={{
        padding:'10px 20px'
      }}>
          <div>
            <label className={'major_visual_teach_team_label'}>实训基地数量：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.num}个</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>工位数：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.workNum}个</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>设备总值：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.Equipment}万元</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>生均仪器设备值：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.AverageInstrument}</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>实训室使用率：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.UtilizationRate}</span>
          </div>
        <div>
          <label className={'major_visual_teach_team_label'}>校外实训基地数量：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.OutSchoolNum}个</span>
        </div>
      </Col>
    )
  }
}
