// 师资队伍
import React, {Component} from 'react';
import {Col} from 'antd';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import jsonData from '../../../data/专业视角-毕业就业情况.json';
import echarts from 'echarts'

export default class GraduateEmployment extends Component {
 
  render(){
    let option = {
      title: {
        show:false
      },
      xAxis: {
        data: jsonData.RECORDS.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          },
          formatter: function (params, index) {
            var newParamsName = "";
            var splitNumber = 5;
            var paramsNameNumber = params && params.length;
            if (paramsNameNumber && paramsNameNumber <= 4) {
              splitNumber = 4;
            } else if (paramsNameNumber >= 5 && paramsNameNumber <= 7) {
              splitNumber = 4;
            } else if (paramsNameNumber >= 8 && paramsNameNumber <= 9) {
              splitNumber = 5;
            } else if (paramsNameNumber >= 10 && paramsNameNumber <= 14) {
              splitNumber = 5;
            } else {
              params = params && params.slice(0, 15);
            }

            var provideNumber = splitNumber; //一行显示几个字
            var rowNumber = Math.ceil(paramsNameNumber / provideNumber) || 0;
            if (paramsNameNumber > provideNumber) {
              for (var p = 0; p < rowNumber; p++) {
                var tempStr = "";
                var start = p * provideNumber;
                var end = start + provideNumber;
                if (p === rowNumber - 1) {
                  tempStr = params.substring(start, paramsNameNumber);
                } else {
                  tempStr = params.substring(start, end) + "\n";
                }
                newParamsName += tempStr;
              }

            } else {
              newParamsName = params;
            }
            params = newParamsName;
            return params;
          },
        }
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show: false
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      series: [
        {
          type: 'bar',
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(
                0, 0, 0, 1,
                [
                  {offset: 0, color: 'rgba(0,207,255,1)'},
                  {offset: 0.5, color: 'rgba(0,207,255,0.8)'},
                  {offset: 1, color: 'rgba(0,207,255,0.19)'}
                ]
              )
            }
          },
          label: {
            normal: {
              color:'#fff',
              show: true,
              position: 'top'
            }
          },
          data: jsonData.RECORDS.data
        }
      ]
    };
    return(
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='GraduateEmploymentBar'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
