/**
 *柱状图
 */
import React,{ Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Col, Spin  } from 'antd';

class TipInfoComp extends Component{
   
	componentDidMount() {
	}
	render() {
		return(
            <Col span={24} className={this.props.imgtype === 'fullscreen' ? "initInfo-content fullscreen" : "initInfo-content"} style={{ hgieght: this.props.height ? this.props.height:'100%'}}>
                {this.props.data === 'nodata' ? <Col span="24" className="nodata">暂无数据...</Col> : <Spin size="large"/>}
            </Col>
		)
	}
}

export default withRouter(TipInfoComp)