/**
 *柱状图
 */
import React,{ Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Col } from 'antd';
import echarts from 'echarts';
import TipInfoComp from './TipInfoComp'


class BarEcharts extends Component{
  
	componentDidMount() {
	}
	render() {
        let chartData = this.props.data;
        if (chartData && chartData !== 'nodata'){
            const axisColor = '#BBBBBB';
            let option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow'
                    }
                },
                legend: {
                    show: false,
                    data: [],
                },
                grid: {
                    left: '5%',
                    right: '5%',
                    bottom: '8%',
                    top: '15%',
                    containLabel: true
                },
                dataZoom: [],
                xAxis: {
                    type: 'category',
                    boundaryGap: true,
                    axisTick: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLine: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLabel: {
                        color: axisColor
                    },
                    splitLine:{
                        show:false,
                        lineStyle:{
                            color: '#d3d3d3',
                            opacity:0.4
                        }
                    }
                },
                yAxis: {
                    type: 'value',
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: '#d3d3d3',
                            opacity:0.4
                        }
                    },
                    axisTick: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLine: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLabel: {
                        color: axisColor
                    } 
                },
                barMaxWidth: 30,
                series: []
            };
            if (chartData.legend) {
                option.legend = chartData.legend;
            }
            if (chartData.grid) {
                option.grid = chartData.grid;
            }
            // 处理数据区域缩放
            if (chartData.dataZoom) {
                option.dataZoom = chartData.dataZoom;
            }
            // 处理图例
            if (chartData.tooltip) {
                option.tooltip = chartData.tooltip;
            }
            // 处理title
            if (chartData.title) {
                option.title = chartData.title;
            }
            // 处理barMaxWidth
            if (chartData.barMaxWidth) {
                option.barMaxWidth = chartData.barMaxWidth;
            }
            // 处理color
            if (chartData.color) {
                option.color = chartData.color;
            }
            // 处理X轴
            if (chartData.xAxis && chartData.xAxis.show !==undefined) {
                option.xAxis.show = chartData.xAxis.show;
            }
            if (chartData.xAxis && chartData.xAxis.type) {
                option.xAxis.type = chartData.xAxis.type;
            }
            if(chartData.xAxis && chartData.xAxis.show){
                option.xAxis.show = chartData.xAxis.show;
            }
            if (chartData.xAxis && chartData.xAxis.data) {
                option.xAxis.data = chartData.xAxis.data;
            }
            if (chartData.xAxis && chartData.xAxis.axisTick) {
                option.xAxis.axisTick = chartData.xAxis.axisTick;
            }
            if (chartData.xAxis && chartData.xAxis.axisLabel) {
                option.xAxis.axisLabel = chartData.xAxis.axisLabel;
            }
            if (chartData.xAxis && chartData.xAxis.axisLine) {
                option.xAxis.axisLine = chartData.xAxis.axisLine;
            }
            if (chartData.xAxis && chartData.xAxis.splitLine) {
                option.xAxis.splitLine = chartData.xAxis.splitLine;
            }
            if (chartData.xAxis && chartData.xAxis.name) {
                option.xAxis.name = chartData.xAxis.name;
            }
            if (chartData.xAxis && chartData.xAxis.nameLocation) {
                option.xAxis.nameLocation = chartData.xAxis.nameLocation;
            }
            if (chartData.xAxis && chartData.xAxis.min) {
                option.xAxis.min = chartData.xAxis.min;
            }
            if (chartData.xAxis && chartData.xAxis.max) {
                option.xAxis.max = chartData.xAxis.max;
            }
            if (chartData.xAxis && chartData.xAxis.interval) {
                option.xAxis.interval = chartData.xAxis.interval;
            }
            // 处理Y轴
            if (chartData.yAxis && chartData.yAxis.show !==undefined) {
                option.yAxis.show = chartData.yAxis.show;
            }
            if (chartData.yAxis && chartData.yAxis.type) {
                option.yAxis.type = chartData.yAxis.type;
            }
            if (chartData.yAxis && chartData.yAxis.data) {
                option.yAxis.data = chartData.yAxis.data;
            }
            if (chartData.yAxis && chartData.yAxis.axisTick) {
                option.yAxis.axisTick = chartData.yAxis.axisTick;
            }
            if (chartData.yAxis && chartData.yAxis.axisLabel) {
                option.yAxis.axisLabel = chartData.yAxis.axisLabel;
            }
            if (chartData.yAxis && chartData.yAxis.axisLine) {
                option.yAxis.axisLine = chartData.yAxis.axisLine;
            }
            if (chartData.yAxis && chartData.yAxis.splitLine) {
                option.yAxis.splitLine = chartData.yAxis.splitLine;
            }
            if (chartData.yAxis && chartData.yAxis.name) {
                option.yAxis.name = chartData.yAxis.name;
            }
            if(chartData.yAxis && chartData.yAxis.nameTextStyle){
                option.yAxis.nameTextStyle = chartData.yAxis.nameTextStyle;
            }
            if (chartData.yAxis && chartData.yAxis.nameLocation) {
                option.yAxis.nameLocation = chartData.yAxis.nameLocation;
            }
            if (chartData.yAxis && chartData.yAxis.min) {
                option.yAxis.min = chartData.yAxis.min;
            }
            if (chartData.yAxis && chartData.yAxis.max) {
                option.yAxis.max = chartData.yAxis.max;
            }
            if (chartData.yAxis && chartData.yAxis.interval) {
                option.yAxis.interval = chartData.yAxis.interval;
            }
            option.series = chartData.series;
            let id = this.props.id;
            let Interval = setInterval( () => {
                if (document.getElementById(id)){
                    clearInterval(Interval);
                    let myCharts = echarts.init(document.getElementById(id));
                    myCharts.off('click');
                    myCharts.resize();
                    //myCharts.clear();
                    myCharts.setOption(option);
                   
                    /**
                     * 点击统计图
                     */
                    if (this.props.clickChart) {
                        myCharts.on('click', (params) => {
                            this.props.clickChart(params);
                        })
                    }
                }
            },20)
        }
		return(
            <div style={{height: this.props.height?this.props.height:'300px'}}>
               {chartData && chartData !== 'nodata'? <Col span={24} className="bar_echarts" id={this.props.id} style={{height: this.props.height?this.props.height:'300px'}}>
                </Col> :<TipInfoComp data={chartData} imgtype={this.props.imgtype}></TipInfoComp>}
            </div>
            
			// <Col span={24} className="bar_echarts" id={this.props.id} style={{height: this.props.height?this.props.height:'300px'}}>
            //     <TipInfoComp data={chartData}></TipInfoComp>
            // </Col>
		)
	}
}

export default withRouter(BarEcharts)