// 招生情况
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
export default class SelectionSituation extends Component {
  constructor(props){
    super(props);
    this.state = {
      plan:'1516',
      number:'934',
      firstRate:'',
      reportRate:''
    }
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff"}}>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.plan}</div>
          <div className={'major_visual_selection_situation_title'}>招生计划</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.number}</div>
          <div className={'major_visual_selection_situation_title'}>录取人数</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.firstRate}</div>
          <div className={'major_visual_selection_situation_title'}>第一志愿报考率</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.reportRate}</div>
          <div className={'major_visual_selection_situation_title'}>报到率</div>
        </Col>
      </Row>
    )
  }
}
