import React, { Component } from 'react'
import { Carousel} from 'antd'
import BasicInfo from './components/BasicInfo'
import SelectionSituation from './components/SelectionSituation'
import TeachResource from './components/TeachResource'
import TeachReform from './components/TeachReform'
import TeachTeam from './components/TeachTeam'
import TrainingBase from './components/TrainingBase'
import CooperativeEnterprise from './components/CooperativeEnterprise'
import GraduateEmployment from './components/GraduateEmployment'
import StudentAward from './components/StudentAward'
export default class Zhuanye extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                // console.log("before change", currentSlide, nextSlide);
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    return (
      <Carousel ref={slider => (this.slider = slider)} {...settings} >
        <div>          
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                基本情况
                            </span>

              </div>
              <div className='item-chart'>
              <BasicInfo></BasicInfo>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                招生情况
                            </span>

              </div>
              <div className='item-chart'>
              <SelectionSituation></SelectionSituation>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教学资源
                            </span>

              </div>
              <div className='item-chart'>
              <TeachResource></TeachResource>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教学改革
                            </span>

              </div>
              <div className='item-chart'>
              <TeachReform></TeachReform>
              </div>              
            </div>
          </div>    

          <div className='item-wrapper-4'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                师资队伍
                            </span>

              </div>
              <div className='item-chart'>
              <TeachTeam></TeachTeam>
              </div>              
            </div>
          </div>   
            
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                实训条件-实训基地
                            </span>

              </div>
              <div className='item-chart'>
              <TrainingBase></TrainingBase>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                培养效果-学生获奖情况
                            </span>

              </div>
              <div className='item-chart'>
              <StudentAward/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                培养效果-毕业就业情况
                            </span>

              </div>
              <div className='item-chart'>
              <GraduateEmployment/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                校企合作-合作企业
                            </span>

              </div>
              <div className='item-chart'>
              <CooperativeEnterprise/>
              </div>              
            </div>
          </div>        
        </div>
        {/* 每一页的划分 */}        
        
      </Carousel>
    )
  }
}
