// 学生获奖情况
import React, {Component} from 'react';
import {Col} from 'antd';
import LineEcharts from '../../../common/components/BaseEcharts/LineEcharts';
import jsonData from '../../../data/major/专业视角--培养效果--学生获奖情况.json';

export default class StudentAward extends Component {
  
  render(){
    let option = {
      title: {
        show: false
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        },
        data: jsonData.RECORDS.axis
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          color:'#00CFFF'
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      color:['#30CCF4','#DED324','#EE6AE6'],
      series: [
        {
          name:'国家级',
          type:'line',
          data:jsonData.RECORDS.country
        },
        {
          name:'省级',
          type:'line',
          data:jsonData.RECORDS.province
        },
        {
          name:'市级',
          type:'line',
          data:jsonData.RECORDS.city
        }
      ]
    };

    return(
      <Col span={24} style={{height:'100%'}}>
        <LineEcharts
          id='StudentAwardLine'
          data={option}
          height='100%'/>
      </Col>
    )
  }
}
