/**
 *饼状图
    series:[
        {
            name:'pieName',
            radius : [30, 110],
            center : ['75%', '50%'],
            type:'pie',
            roseType : 'area',
            data:[
                {value:10, name:'rose1'},
                {value:5, name:'rose2'},
            ]
        }
    ]
 */
import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import {Col} from 'antd';
import echarts from 'echarts/lib/echarts';
import TipInfoComp from './TipInfoComp'

class PieEcharts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            chart: ''
        }
    }
    componentDidMount() {
        // this.createChart(this.props.data);
    }
    render() {
        let chartData = this.props.data;
        if(chartData && chartData !== 'nodata'){
            let option = {
                tooltip: {
                    trigger: 'item',
                    formatter: '{a} <br/>{b} : {c} ({d}%)',
                    position: 'right'
                },
                series: []
            };
            if(chartData.graphic){
                option.graphic = chartData.graphic;
            }
            if (chartData.legend) {
                option.legend = chartData.legend;
            }
            // 处理color
            if (chartData.color) {
                option.color = chartData.color;
            }
            if (chartData.tooltip) {
                option.tooltip = chartData.tooltip;
            }
            if (chartData.series) {
                option.series = chartData.series
                
            }
            if (chartData.title) {
                option.title = chartData.title
            }
            let id = this.props.id;
            let Interval = setInterval(() => {
                if (document.getElementById(id)) {
                    clearInterval(Interval);
                    let myCharts = echarts.init(document.getElementById(id));
                    myCharts.off('click');
                    myCharts.resize();
                    // myCharts.clear();
                    myCharts.setOption(option);
                    /**
                     * 点击统计图
                     */
                    if (this.props.clickChart) {
                        myCharts.on('click', (params) => {
                            this.props.clickChart(params);
                        })
                    }
                }
            }, 20)
        }
        return (
            <div style={{ height: this.props.height ? this.props.height : '400px' }}>
             {chartData && chartData !== 'nodata'?<Col span={24} className="pie_echarts" id={this.props.id} style={{ height: this.props.height ? this.props.height : '400px' }}>
            </Col>:<TipInfoComp data={chartData} imgtype={this.props.imgtype}></TipInfoComp>}
            </div>
            
        )
    }
}

export default withRouter(PieEcharts)