/**
 *折线图
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Col } from 'antd';
import echarts from 'echarts';
import TipInfoComp from './TipInfoComp'

class LineEcharts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            chart: ''
        }
    }
    componentDidMount() {
    }
    render() {
        let chartData = this.props.data;
        if (chartData && chartData !== 'nodata') {
            const axisColor = '#BBBBBB';
            let option = {
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    show: false,
                    textStyle:{
                        color:'#FFF'
                    },
                    data: []
                },
                grid: {
                    left: '5%',
                    right: '8%',
                    bottom: '8%',
                    top: 50,
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: true,
                    max: '',
                    axisTick: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLine: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLabel: {
                        color: axisColor
                    },
                    splitLine:{
                        show:false,
                        lineStyle:{
                            color: '#d3d3d3',
                            opacity:0.4
                        }
                    }
                },
                yAxis: {
                    type: 'value',
                    splitLine: {
                        show:true,
                        lineStyle:{
                            color: '#d3d3d3',
                            opacity:0.4
                        }       
                    },
                    axisTick: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLine: {
                        lineStyle: {
                            color: axisColor
                        }
                    },
                    axisLabel: {
                        color: axisColor
                    }
                },
            };
            if (chartData.dataZoom) {
                option.dataZoom = chartData.dataZoom;
            }
            if (chartData.legend) {
                option.legend = chartData.legend;
            }
            if (chartData.tooltip) {
                option.tooltip = chartData.tooltip;
            }
            if (chartData.grid) {
                option.grid = chartData.grid;
            }
            // 处理color
            if (chartData.color) {
                option.color = chartData.color;
            }
            // 处理X轴
            if (chartData.xAxis && chartData.xAxis.data) {
                option.xAxis.data = chartData.xAxis.data;
            }
            if (chartData.xAxis && chartData.xAxis.axisTick) {
                option.xAxis.axisTick = chartData.xAxis.axisTick;
            }
            if (chartData.xAxis && chartData.xAxis.axisLabel) {
                option.xAxis.axisLabel = chartData.xAxis.axisLabel;
            }
            if (chartData.xAxis && chartData.xAxis.axisLine) {
                option.xAxis.axisLine = chartData.xAxis.axisLine;
            }
            if (chartData.xAxis && chartData.xAxis.splitLine) {
                option.xAxis.splitLine = chartData.xAxis.splitLine;
            }
            if (chartData.xAxis && chartData.xAxis.boundaryGap !== undefined) {
                option.xAxis.boundaryGap = chartData.xAxis.boundaryGap;
            }
            if (chartData.xAxis && chartData.xAxis.name) {
                option.xAxis.name = chartData.xAxis.name;
            }
            if (chartData.xAxis && chartData.xAxis.nameLocation) {
                option.xAxis.nameLocation = chartData.xAxis.nameLocation;
            }
            // y轴
            if (chartData.yAxis && chartData.yAxis.axisTick) {
                option.yAxis.axisTick = chartData.yAxis.axisTick;
            }
            if (chartData.yAxis && chartData.yAxis.axisLabel) {
                option.yAxis.axisLabel = chartData.yAxis.axisLabel;
            }
            if (chartData.yAxis && chartData.yAxis.axisLine) {
                option.yAxis.axisLine = chartData.yAxis.axisLine;
            }
            if (chartData.yAxis && chartData.yAxis.splitLine) {
                option.yAxis.splitLine = chartData.yAxis.splitLine;
            }
            if (chartData.yAxis && chartData.yAxis.name) {
                option.yAxis.name = chartData.yAxis.name;
            }
            if (chartData.yAxis && chartData.yAxis.nameLocation) {
                option.yAxis.nameLocation = chartData.yAxis.nameLocation;
            }
            if (option.yAxis && option.yAxis.max) {
                option.yAxis.max = chartData.yAxis.max;
            }
            option.series = chartData.series;
            let id = this.props.id;
            // let myCharts = echarts.init(document.getElementById(id));
            let Interval = setInterval(() => {
                if (document.getElementById(id)) {
                    clearInterval(Interval);
                    let myCharts = echarts.init(document.getElementById(id));
                    myCharts.off('click');
                    myCharts.resize();
                    myCharts.setOption(option);
                    /**
                     * 点击统计图
                     */
                    if (this.props.clickChart) {
                        myCharts.on('click', (params) => {
                            this.props.clickChart(params);
                        })
                    }
                }
            }, 20)
        }
        return (
            <div style={{ height: this.props.height ? this.props.height : '300px' }}>
               {chartData && chartData !== 'nodata'?<Col span={24} className="bar_echarts" id={this.props.id} style={{ height: this.props.height ? this.props.height : '300px' }}>
               </Col>:<TipInfoComp data={chartData} imgtype={this.props.imgtype}></TipInfoComp>}
            </div>
            
        )
    }
}
export default withRouter(LineEcharts)